<template>
  <div id="app">
    <h1>在线翻译</h1> 
    <h5 >简单 / 医用 / 便捷</h5>
    <translateform  @formSubmit="translateText"></translateform>
    <br>
    <translateOutput v-text="translatedText"></translateOutput>
  </div>
</template>

<script>
import Translateform from "./components/Translateform"
import TranslateOutput from './components/TranslateOutput'
export default {
  name:'app',
  data:function(){
    return {
      translatedText:''
    }
  },
  components: {
    'translateform':Translateform,
     TranslateOutput
  },
  methods:{
    translateText:function(text,language){
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20181207T132219Z.cdbfe4b08f46ca9c.45e228bcccf55594b34efc5d684919e116eeca5c&lang='+language+'&text='+text)
      .then((response)=>{
        // console.log(response.body.text[0]);
        this.translatedText = response.body.text[0];
      })
    }
  }
}
</script>

